/**
 * 导出所有模块需要用到接口
 * 一级属性：模块名
 * 一级属性中的方法：当前模块需要用的接口
 * @type {Object}
 */

/*用户权限*/
import build from './build/index.js';

import proxy from './proxy/index.js';


export default [{
    module: 'build',
    name: '页面构建模块',
    list: build
},{
    module: 'proxy',
    name: '代理表接口',
    list: proxy
}];



