import * as types from './mutations_types'

import {
    store
} from 'utils/';

export default {
    [types.SHOW_LOADING](state) {
        state.request_num +=1;
        state.ajax_loading = true;
    },

    [types.HIDE_LOADING](state) {
        state.request_num -= 1;
        if(state.request_num <= 0){
            state.request_num = 0;
            state.ajax_loading = false;
        }
    },

    update (state, data){
        state.tree_nodes = data;
    }
};


