import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

import { Login, Build, ProxyTable} from 'views/';


export default new Router({
	routes: [
		{
			path  : '/',
			name  : 'home',
			hidden: true,
			redirect(to){
				return 'build';
			}
		},
		{
			path     : '/login',
			name     : '登录',
			hidden   : true,
			component: Login
		},
		{
			path: '/build',
			name: '前端页面构建',
			icon: 'inbox',
			component: Build
		},
		{
			path: '/proxyTable',
			name: '代理表维护',
			icon: 'inbox',
			component: ProxyTable
		}
	]
})


