<template>
    <div>
        <el-input
                type='input'
                class="form-data-input"
                v-model="submit_data[data.key]"
                v-bind="attrs"
                :placeholder="data.desc"
                :disabled="data.readonly"
                @blur="onBlur"
                @focus="onFocus"
                @change="onChange">
            <el-button
                    v-if="data.withBtn"
                    slot="append"
                    :type="data.withBtn.type"
                    @click="inputBtnEvent(data.key)">{{data.withBtn.text}}</el-button>
        </el-input>
    </div>
</template>
<script>
    import Common from './js/Common';
	import InputTextarea from './js/InputTextarea';
    var Js=Common('sls-input');
	Js.mixins=[InputTextarea];
	export default Js;
</script>
<style scoped lang="less">
</style>


