<template>
    <el-form style=""
             :label-width="setting.labelWidth+'px'"
             ref='form-data'
             :inline="setting.inline"
             :rules='rules'
             :model='submit_data'>
        <el-row>
            <el-col v-for='(field,index) in fields' v-if="!field.hidden" :span="span" :key='index'>
                <el-form-item
                        class='edit-form'
                        v-bind="field.attrs || {}"
                        :label="field.label"
                        :prop='field.key'>
                    <component
                            :Data="field"
                            :SubmitData="submit_data"
                            :SubmitInfo="submit_info"
                            :TempFieldObj="temp_field_obj"
                            :is="components[field.type] || 'SlsInput'"></component>
                    <i class="validating el-icon-loading"></i>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item align="center">
            <el-button type="primary" @click='onSubmit("form-data")'>保存</el-button>
            <el-button class="style-self" @click='onCancel'>取消</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
	import FormDataJs from './FormData.js';
	export default FormDataJs;
</script>
<style scoped lang='less'>
    .demo-form-inline {
        display: inline-block;
        float: right;
    }
    .style-self{
        margin-left: 50px;
    }
    .edit-form{
        position: relative;
    }
    .validating{
        display: none;
        position: absolute;
        top: 11px;
        right: 9px;
    }
    .is-validating .validating{
        display: inline-block;
    }
</style>



