import { HeadNav } from "layout/"

export default {
  name: "build",
  data() {
    return {
      page: "",
      pages: [],
      btnContent: "",
      isBuilding: "",
      //记录定时器
      timer: null,
      steps: []
    }
  },
  components: {
    HeadNav
  },
  methods: {
    init() {
      var token = this.$store.state.user.userinfo.token;
      if(token == "login"){
        this.getStatus(false);
      }else{
        this.$router.push('/login');
      }
    },

    /*获取当前状态*/
    getStatus(hidden) {
      this.$$api_build_getStatus({
        defFn: data=>{
          this.steps = data.steps;
          if(data.status == "null"){
            this.btnContent = "开始构建";
            this.isBuilding = false;
          }else{
            this.btnContent = "正在构建中！请稍等！";
            this.isBuilding = true;
            this.startTimeout();
          }
          this.pages.length == 0 && this.initDropMenu(data.pages);
        },
        opts: {hd: hidden}
      });
    },

    /*初始化下拉框*/
    initDropMenu(pages) {
      Object.keys(pages).forEach((item,index)=>{
        index == 0 && (this.page = item);
        this.pages.push({
          label: pages[item].name,
          value: item
        });
      });
    },

    startBuild() {
      this.btnContent = "正在构建中！请稍等！";
      this.isBuilding = true;
      this.$$api_build_start({
        data: {
          path: this.page
        },
        defFn: data=> {
          if (data.status == "started") {
            this.startTimeout();
            this.steps = data.steps;
          }else{
            this.$message({
              message: '未能启动构建程序，请SSH到服务器查看日志！',
              type: 'warning'
            });
          }
        }
      });
    },

    startTimeout() {
      this.timer = setTimeout(()=>{
        this.getStatus(true);
      },3000);
    },

    /*重置构建状态*/
    resetBuild() {
      this.$$api_build_resetStatus({
        defFn: data=>{
          if(data.status == "null"){
            this.btnContent = "开始构建";
            this.isBuilding = false;
            this.$message({
              message: '已终止页面构建',
              type: 'success'
            });
          }
        }
      });
    }
  },
  beforeMount() {
    this.init();
  }
}


