<template>
    <div class="proxy-table" v-loading.fullscreen.lock="$store.state.global.ajax_loading">
        <head-nav></head-nav>
        <div class="main-body">
            <list-data
                    :List="listData"
                    :FieldList="fieldList"
                    :BtnInfo="btnInfo"
                    @onClickBtnAdd="onListAddBtn"
                    @onClickBtnUpdate="onListUpdateBtn"
                    @onClickBtnDelete="deleteProxyItem"></list-data>
            <el-dialog
                    :title="dialogTitle" width="34%"
                    custom-class="min-width400"
                    :visible.sync="dialogVisible">
                <form-data
                        ref="form-data"
                        :FieldList="fieldList"
                        :Setting="setting"
                        :Rules="rules"
                        :DefaultValue="defaultValue"
                        @onCancel="onCancel"
                        @onSubmit="createOrUpdateProxyItem"></form-data>
            </el-dialog>
        </div>
    </div>
</template>
<script>
    import ProxyTable from './ProxyTable.js';
    export default ProxyTable;
</script>
<style scoped lang='less'>
    .main-body {
        width: 80%;
        margin: 60px auto 0px;
        padding: 10px;
        .margin-top10 {
            margin-top: 10px;
            .el-alert{
               margin-top: 10px;
            }
        }
    }
</style>


