import { HeadNav } from "layout/"

export default {
  name: "build",
  data() {
    return {
      /*表格相关属性*/
      listData: [],
      fieldList: [
        {label: "源URL", key: "source"},
        {label: "目标主机", key: "target"},
        {label: "路径重写", key: "pathRewrite"}
      ],
      btnInfo: {
        width: 134,
        batch: false,
        select: false,
        update_text: "编辑",
        delete_text: "删除"
      },

      /*dialog相关属性*/
      dialogVisible: false,
      dialogTitle: "",
      setting: {
        labelWidth: 72
      },
      rules: {
        source: [{ required: true, message: "请输入源URL！", trigger: 'blur' }],
        target: [{ required: true, message: "请输入目标主机！", trigger: 'blur' }],
        pathRewrite: [{ required: true, message: "请输入路径重写！", trigger: 'blur' }]
      },
      defaultValue: {}

    }
  },
  components: {
    HeadNav
  },
  methods: {
    init() {
      var token = this.$store.state.user.userinfo.token;
      if(token == "login"){
        this.getProxyList();
      }else{
        this.$router.push('/login');
      }
    },

    /*获取代理表*/
    getProxyList() {
      this.$$api_proxy_getProxyList({
        defFn: data=>{
          this.listData = data;
        }
      });
    },

    /*添加代理条目*/
    onListAddBtn() {
      this.dialogVisible = true;
      this.dialogTitle = "新增条目";
      this.resForm();
      this.defaultValue = {id: -1};
    },

    /*更新代理条目*/
    onListUpdateBtn(option) {
      this.dialogVisible = true;
      this.dialogTitle = "编辑条目";
      this.resForm();
      var row = {
        id: option.dataIndex
      };
      this.defaultValue = this.$$$.extend(row,option.data);
    },

    /*取消添加代理条目*/
    onCancel() {
      this.dialogVisible = false;
    },

    /*创建或更新proxy条目*/
    createOrUpdateProxyItem() {
      this.$$api_proxy_createOrUpdateProxyItem({
        data: this.defaultValue,
        fn: data=>{
          this.listData = data;
          this.onCancel();
        }
      });
    },

    /*删除proxy条目*/
    deleteProxyItem(option) {
      this.$alert('确定要删除该条目！', '警告：', {
				confirmButtonText: '确定',
				type: "warning",
				callback: action => {
          if(action == "cancel") return;
          this._deleteProxyItem(option.dataIndex);
        }
      });
    },
    _deleteProxyItem(id) {
      this.$$api_proxy_deleteProxyItem({
        data: {id},
        fn: data=>{
          this.listData = data;
        }
      });
    },

    /*重置form表单*/
    resForm() {
      this.$refs['form-data'] && this.$refs['form-data'].resetFields();
    }
  },
  beforeMount() {
    this.init();
  }
}


