import $ from 'jquery';
//import lib__ from 'underscore';

// console.log(lib__);
function createFileByblob(name, bolb) {
	var reader = new FileReader();
	reader.readAsDataURL(bolb);
	reader.onload = function (e) {
		var a = document.createElement('a');
		a.download = name;
		a.href = e.target.result;
		$("body").append($(a));
		a.click();
		$(a).remove();
	};
}

export default {
	$,
	createFileByblob
};


