 * Created by Nantian on 2018/7/22.
-->
<template>
    <div class="build" v-loading.fullscreen.lock="$store.state.global.ajax_loading">
        <head-nav></head-nav>
        <div class="main-body">
            <span>选择构建页面：</span>
            <el-select
                    style="display: inline-block"
                    v-model="page">
                <el-option
                        v-for="(item,index) in pages"
                        :key="index"
                        :label="item.label"
                        :value="item.value"></el-option>
            </el-select>
            <el-button
                    type="primary"
                    :loading="isBuilding"
                    @click="startBuild">{{btnContent}}</el-button>
            <!--<el-button
                    type="danger"
                    @click="resetBuild">强制终止构建</el-button>-->
            <div class="margin-top10">
                <el-alert
                        v-for="(item,index) in steps" :key="index" :title="item.msg"
                        show-icon :close-text="item.time" :type="item.type">
                </el-alert>
            </div>
        </div>
    </div>
</template>
<script>
    import Build from './Build.js';
    export default Build;
</script>
<style scoped lang='less'>
    .main-body {
        width: 80%;
        margin: 60px auto 0px;
        padding: 10px;
        .margin-top10 {
            margin-top: 10px;
            .el-alert{
               margin-top: 10px;
            }
        }
    }
</style>


