import request from 'apis/';
import { gbs } from 'config/';
import _ from 'underscore';

var apiList = {};
/**
 * 通过api名称获取api对应得url
 * */
_.each(request,(item)=>{
  _.each(item.list,(v)=>{
    apiList['$$api_'+item.module+'_'+v.method] = {
      url:gbs.host+v.path,
      type:v.type
    };
  });
});

/**
 * 通过id获取当前节点和所有上级节点
 * */
function getParentNodes(id){
  var treeObj = this.$store.state.global.tree_nodes;
  var pNodes = {},

    cur = treeObj.getNodeByParam("id", id);
  for (; ;) {
    var type_id = /^([A-Z,a-z]+_[0-9]+)_([0-9\-a-f]+$)/.exec(cur.id);
    pNodes[type_id[1]] = cur;
    if (cur.pId) {
      cur = cur.getParentNode();
    } else {
      break;
    }
  }
  return pNodes;
}

export {
  apiList,
  getParentNodes,
}


