<template>
    <div class="home" v-loading.fullscreen.lock="$store.state.global.ajax_loading">
        <head-nav v-if="displayApp"></head-nav>
        <div class="left-fixed-right-auto" v-if="displayApp">
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
    import HeadNav from '../head-nav/HeadNav.vue';

    export default {
        name: 'home',
        data: function () {
            return {
                displayApp: true,
                show: true
            }
        },
        components:{HeadNav},
        methods: {
            init(){
                /*
                * 校验是否已经是登录用户访问
                *  location.replace("http://128.192.138.98:7001/iomp-portal-web/login.jsp");
                }*/
                this.$$api_gateway_checkSt({
                    fn: data=>{
                        if(data.code == "00"){
                            /*var userinfo = {
                                access_status: "",
                                address: "",
                                api_routers: "",
                                birthday: "",
                                create_time: "",
                                default_web_routers: "",
                                email: "",
                                id: data.User_Info.User_Id,
                                is_update_pass: "",
                                pid: "",
                                sex: "",
                                status: "",
                                token: "",
                                username: data.User_Info.Login_Name,
                                web_routers: "",
                            };*/
                            this.$store.dispatch('update_userinfo', {
                                userinfo: data.user_info
                            }).then(() => {
                                this.show = true;
                            });
                        }else if(data.code !== "98" && data.login_url){
                            this.displayApp = false;
                            this.$store.dispatch('remove_userinfo').then(() => {
                                this.$alert('错误编码：'+data.code + ',错误信息：' + data.message + '！重新登录请按确定键。', '提示:', {
                                    confirmButtonText: '确定',
                                    callback: action => {
                                        //location.replace("http://128.192.138.98:7001/iomp-portal-web/login.jsp");
                                        location.replace(data.login_url);
                                    }
                                });
                            });
                            
                        }else{
                            this.displayApp = false;
                            this.$store.dispatch('remove_userinfo').then(() => {
                                this.$alert('错误编码：'+data.code + ',错误信息：' + data.message + ', 请在云平台门户网站登录！', '提示:', {
                                    confirmButtonText: '确定',
                                    callback: action => {
                                        location.replace("http://128.192.138.98:7001/iomp-portal-web/login.jsp");
                                    }
                                });
                            });
                        }
                    }
                });
            }
        },
        beforeMount: function () {
            //this.init();
        }
    }
</script>
<style scoped lang='less'>
    .left-fixed-right-auto{
        margin-top: 60px;
        background: #FFF;
    }
</style>



